   </v-container>\r\n  </v-main>\r\n</template>\r\n\r\n<script>\r\nimport getModule from \"@/helpers/store/get-module\";\r\nimport {\r\n  VMain,\r\n  VContainer,\r\n  VCard,\r\n  VForm,\r\n  VRow,\r\n  VBtn,\r\n  VIcon,\r\n  VTextField,\r\n  VDivider,\r\n  VCardTitle,\r\n  VCol,\r\n} from \"vuetify/lib\";\r\nexport default {\r\n  components: {\r\n    VMain,\r\n    VCol,\r\n    VContainer,\r\n    VCard,\r\n    VForm,\r\n    VRow,\r\n    VBtn,\r\n    VIcon,\r\n    VTextField,\r\n    VDivider,\r\n    VCardTitle,\r\n  },\r\n  data() {\r\n    return {\r\n      valid: true,\r\n      credentials: {\r\n        password: \"\",\r\n        email: \"\",\r\n      },\r\n      loading: false,\r\n    };\r\n  },\r\n  computed: {\r\n    validationRules() {\r\n      return this.$store.getters.validationRules;\r\n    },\r\n  },\r\n  methods: {\r\n    required(v) {\r\n      return !!v || this.validationRules.required;\r\n    },\r\n    email(v) {\r\n      const pattern =\r\n        /^(([^<>()[\\]\\\\.,;:\\s@\"]+(\\.[^<>()[\\]\\\\.,;:\\s@\"]+)*)|(\".+\"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\\-0-9]+\\.)+[a-zA-Z]{2,}))$/;\r\n      return pattern.test(v) || this.validationRules.email;\r\n    },\r\n    passwordLength(v) {\r\n      return v.length >= 8 || this.validationRules.password_length;\r\n    },\r\n    async login() {\r\n      if (this.$refs.form.validate()) {\r\n        this.loading = true;\r\n        await this.$store.dispatch(\"authLogin\", this.credentials);\r\n        this.loading = false;\r\n      }\r\n    },\r\n  },\r\n  async created() {\r\n    if (!this.$store.hasModule(\"validationRules\"))\r\n      this.$store.registerModule(\r\n        \"validationRules\",\r\n        getModule(\"validationRules\")\r\n      );\r\n    if (!this.$store.hasModule(\"snackbarAlerts\"))\r\n      this.$store.registerModule(\"snackbarAlerts\", getModule(\"snackbarAlerts\"));\r\n\r\n    if (localStorage.getItem(\"user\"))\r\n      this.$router.push({\r\n        name: \"AdminMain\",\r\n      });\r\n    await this.$store.dispatch(\"validationRules\");\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.panel-title-header {\r\n  font-size: 3rem;\r\n}\r\n.button-name {\r\n  color: white;\r\n}\r\n</style>\r\n\r\n"],"sourceRoot":""}]);
